.links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}
.links .link-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
}
.links .link-item:not(:last-child) {
  margin-right: 10px;
}
.links .link-item .link-label {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.5);
  color: #fff;
